<template lang="pug">
  div.container
    .textarea__block
      textarea.textarea__top(v-model="first" placeholder="Места старта, финиша, сервер и время проведения")
      textarea.textarea__top(v-model="desc" placeholder="Доп. информация")

    .input__box
      .input__block
        input.input.link(v-model="link" placeholder="Ссылка на конвой")
        input.input.cargo(v-model="cargo" placeholder="Груз")
        input.input.discord(v-model="discord" placeholder="Discord (Если чужой)")
        button(@click="isAnnounce()") Создать
      .input__block
        .radio
          label.radio
            input(type="radio" v-model="route" value="Оптимальный")
            | Оптимальный
          label.radio
            input(type="radio" v-model="route" value="Кратчайший")
            | Кратчайший
          label.radio
            input(type="radio" v-model="route" value="По региональным дорогам")
            | По региональным дорогам

        .checkbox
          label.checkbox
            input(type="checkbox" v-model="dlc" value="Beyond the Baltic Sea")
            | Beyond the Baltic Sea
          label.checkbox
            input(type="checkbox" v-model="dlc" value="Going East!")
            | Going East!
          label.checkbox
            input(type="checkbox" v-model="dlc" value="Italia")
            | Italia
          label.checkbox
            input(type="checkbox" v-model="dlc" value="Scandinavia")
            | Scandinavia
          label.checkbox
            input(type="checkbox" v-model="dlc" value="Vive la France !")
            | Vive la France !
 
    div.vk
      pre(v-model="info") {{ info.first.server }}
        br
        | {{ info.desc.contact }}
        br
        | {{ info.desc.route }}
        br
        br
        | {{ info.desc.other }}
        br
      pre(v-model="info" v-for="item in info.desc.dlc")
        | {{ item }}
      pre(v-model="info")
        | Старт: {{ info.first.startCity }} ( {{ info.first.startCompany }} )
        br
        | Финиш: {{ info.first.finishCity }} ( {{ info.first.finishCompany }} )
        br
        | {{ info.first.meet }}
        br
        | {{ info.first.exit }}
        br
        | {{ info.desc.cargo }}
    
    hr
    hr
      
    div.discord
      pre(v-model="info") @everyone
        br
        | Старт: {{ info.first.startCity }}
        br
        | {{ info.first.meet }}
        br
        | Подробнее:
        br
        | https://vk.com/vtcda
        br
        | {{ info.desc.vtcpanel }}

    .bottom__block
      textarea.textarea__bottom(placeholder="vk")
      textarea.textarea__bottom(placeholder="discord")
    
</template>


<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      first: "",
      desc: "",
      link: "",
      cargo: "Груз: ",
      route: "Оптимальный",
      discord: "",
      dlc: [],
      info: {
        desc: {
            contact: "",
            other: "",
            vtcpanel: "",
            cargo: "",
            route: "",
            dlc: []
        },
        first: {
            startCity: "",
            startCompany: "",
            finishCity: "",
            finishCompany: "",
            server: "",
            meet: "",
            exit: ""
        },
      }
    };
  },
  methods: {
    isAnnounce() {
      if (this.link !== "") {

        let arr = this.first.split("\n");

        arr.splice(0, 1);
        arr.splice(2, 4);
        arr.splice(4, 1);
        arr.splice(7);

        let n = 0;
        for(let item in this.info.first) {
          this.info.first[item] = arr[n];
          n++
        }

        this.info.desc.other = this.desc;
        this.info.desc.cargo = this.cargo;
        this.info.desc.vtcpanel = this.link;
        this.info.desc.route = `Предпочитаемый маршрут: ${this.route}`;
        if (this.discord === "") {
          this.info.desc.contact = "Связь: https://discord.gg/JSGZVph";
        } else {
          this.info.desc.contact = this.discord;
        }
        this.info.desc.dlc = [];
        this.dlc.forEach(el => {
            this.info.desc.dlc.push(el);
        });
      }
    }
  }
};
</script>

